$orange : #ea961c;
$blue : #455bd7;
$darkBlue : #1333a4;
$lightGray : #f7f8f9;
$gray : #cccccc;
$dark : #333333;


.page-content {
  padding-top: 60px;
  padding-bottom: 114px;
}

.bread {
  display: flex;
  flex-wrap: wrap;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.25;

  a {
    color: $dark;
    text-decoration: underline;

    &:hover {
      text-decoration: none;
    }
  }

  .sep {
    color: $dark;
    margin: 0 5px;
  }

  .current {
    color: $dark;
  }
}


%button {
  -webkit-font-smoothing: antialiased;
  border: none;
  display: inline-block;
  font-family: $font1;
  text-decoration: none;
  user-select: none;
  border-radius: 30px;
  padding: 20px 40px 21px;
  cursor: pointer;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.2;

  &:hover {}

  &:focus {
    outline: none;
  }
}

.svg-filters {
  position: absolute;
  visibility: hidden;
  width: 1px;
  height: 1px;
}

.btn-orange {
  @extend %button;
  color: #ffffff;
  background: none;
  position: relative;
  z-index: 0;


  -webkit-filter: none !important;
  filter: none !important;

  .button__bg {
    .safari & {
      -webkit-filter: none !important;
      filter: none !important;
    }
  }


  .button__container {
    position: absolute;
    display: block;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    pointer-events: none;
    z-index: -1;
    transform: translateZ(0);
    transition: all 0.1s ease-out;
  }

  .button__bg,
  .circle {
    background: $orange;
    animation: hue-rotate 10s linear infinite;
  }

  .button__bg {
    background: $orange;
    position: absolute;
    @extend %button;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    box-sizing: border-box;
  }

  .circle {
    position: absolute;
    width: 30px;
    height: 30px;
    border-radius: 15px;
    background: $orange;

    &.top-left {
      top: 10px;
      left: 10px;
    }

    &.bottom-right {
      bottom: 10px;
      right: 10px;
    }
  }
}

.btn-blue {
  @extend .btn-orange;

  .button__bg,
  .circle {
    background: $blue;
  }

  .button__bg {
    background: $blue;
  }

  .circle {
    background: $blue;
  }
}



.h1 {
  font-size: 50px;
  line-height: 62px;
}

.h2 {
  font-size: 40px;
  line-height: 46px;
}

.h3 {
  font-size: 30px;
  line-height: 36px;
}

.h4 {
  font-size: 20px;
  line-height: 26px;
}

.p {
  font-size: 16px;
  line-height: 26px;

  a {
    color: $blue;
    text-decoration: underline;

    &:hover {
      text-decoration: none;
    }
  }
}

.ul {
  list-style: none;

  li {
    padding-left: 29px;
    font-size: 16px;
    line-height: 26px;
    position: relative;

    &::before {
      content: "";
      display: block;
      width: 5px;
      height: 5px;
      border-radius: 100%;
      background-color: $blue;
      position: absolute;
      left: 0;
      top: 11px;
    }
  }
}

.ol {
  list-style: none;
  counter-reset: my-counter;

  li {
    padding-left: 29px;
    font-size: 16px;
    line-height: 26px;
    position: relative;
    counter-increment: my-counter;

    &::before {
      content: counter(my-counter) ".";
      display: block;
      position: absolute;
      left: 0;
      top: 6px;
      color: $blue;
      font-size: 16px;
      font-weight: 700;
      line-height: 1em;
    }
  }
}

.pagetitle {
  margin-top: 10px;
  margin-bottom: 55px;
}

%margin_h {
  &:not(:last-child) {
    margin-bottom: 32px;
  }

  &:not(:first-child) {
    margin-top: 53px;
  }

  @media all and (max-width: 760px) {
    &:not(:last-child) {
      margin-bottom: 26px;
    }

    &:not(:first-child) {
      margin-top: 43px;
    }
  }
}

%margin_ul {
  &:not(:last-child) {
    margin-bottom: 53px;
  }

  &:not(:first-child) {
    margin-top: 53px;
  }

  @media all and (max-width: 760px) {
    &:not(:last-child) {
      margin-bottom: 43px;
    }

    &:not(:first-child) {
      margin-top: 43px;
    }
  }
}

.text-style {
  font-size: 16px;
  line-height: 26px;

  h1 {
    @extend .h1;
    @extend %margin_h;
  }

  h2 {
    @extend .h2;
    @extend %margin_h;
  }

  h3 {
    @extend .h3;
    @extend %margin_h;
  }

  h4 {
    @extend .h4;
    @extend %margin_h;
  }

  ul {
    @extend .ul;
    @extend %margin_ul;
  }

  ol {
    @extend .ol;
    @extend %margin_ul;
  }

  p {
    @extend .p;

    +p {
      margin-top: 1em;
    }
  }
}


.input {
  color: $dark;
  font-family: $font1;
  font-size: 16px;
  font-weight: 400;
  padding: 14px 22px 15px;
  box-sizing: border-box;
  width: 303px;
  border-radius: 6px;
  background-color: $lightGray;
  border: 1px solid $lightGray;
  margin: 0;

  &::placeholder {
    font-family: $font1;
    font-size: 16px;
    font-weight: 400;
    color: $dark;
    opacity: 0.7;
  }

  &.not-empty,
  &:focus {
    outline: none;
    border-color: $blue;
    background: #eceffb;
  }

  &.error {
    border-color: $orange;
    background: #fdf4e8;
  }

}

.inputfile {
  width: 0.1px;
  height: 0.1px;
  opacity: 0;
  overflow: hidden;
  position: absolute;
  z-index: -1;

  +label {
    color: $dark;
    font-family: $font1;
    font-size: 16px;
    font-weight: 400;
    background-color: $lightGray;
    border: 1px solid $lightGray;
    display: inline-block;
    cursor: pointer;
    margin: 0;
    padding: 14px 54px 15px 22px;
    width: 303px;
    box-sizing: border-box;
    border-radius: 6px;
    position: relative;

    * {
      pointer-events: none;
    }

    &:hover {
      border-color: $blue;
      background: #eceffb;

      &::after {
        background-image: url("../img/input-file-select.svg");
      }
    }

    &::after {
      content: "";
      display: inline-block;
      width: 21px;
      height: 21px;
      background: url("../img/input-file.svg") center center no-repeat;
      background-size: contain;
      position: absolute;
      right: 19px;
      top: calc(50% - 10px);
    }
  }

  &.has-file,
  &:focus {
    +label {
      border-color: $blue;
      background: #eceffb;

      &::after {
        background-image: url("../img/input-file-select.svg");
      }
    }
  }
}

.checkbox {
  input {
    position: absolute;
    z-index: -1;
    opacity: 0;
    margin: 0;

    +span {
      position: relative;
      padding: 0 0 0 26px;
      cursor: pointer;

      &::before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 22px;
        height: 22px;
        border-radius: 4px;
        border: 2px solid $orange;
        background-color: #ffffff;
        box-sizing: border-box;
      }

      &::after {
        content: "";
        display: none;
        position: absolute;
        left: 9px;
        top: 5px;
        width: 6px;
        height: 9px;
        border: solid white;
        border-width: 0 3px 3px 0;
        transform: rotate(45deg);
        box-sizing: border-box;
      }
    }

    &:checked {
      +span {
        &::before {
          background: $orange;
        }

        &::after {
          display: block;
        }
      }
    }

    &:not(:checked):focus,
    &:not(:checked):hover {
      +span {
        &:after {
          display: block;
          border-color: $orange;
        }
      }
    }
  }
}

.close-btn {
  display: inline-block;
  width: 41px;
  height: 41px;
  border-radius: 100%;
  padding: 0;
  margin: 0;
  border: none;
  cursor: pointer;
  background: transparent url("../img/close.svg") center center no-repeat;
  background-size: 15px;

  &:hover {
    background-color: $orange;
    background-image: url("../img/close-hover.svg");
  }
}



.circle-btn {
  cursor: pointer;
  padding: 0;
  margin: 0;
  display: inline-block;
  width: 50px;
  height: 50px;
  box-sizing: border-box;
  border-radius: 100%;
  border: 2px solid #d6d6d6;
  background-color: #ffffff;
  background-size: 13px 13px;
  background-repeat: no-repeat;
  background-position: center center;

  &.arr-left {
    background-image: url("../img/arr-left.svg")
  }

  &.arr-right {
    background-image: url("../img/arr-right.svg")
  }

  &.arr-up {
    background-image: url("../img/arr-up.svg")
  }

  &.arr-down {
    background-image: url("../img/arr-down.svg")
  }

  &.zoom {
    background-image: url("../img/zoom.svg")
  }

  &:hover {
    border-color: $orange;
    background-color: $orange;

    &.arr-left {
      background-image: url("../img/arr-left-hover.svg")
    }

    &.arr-right {
      background-image: url("../img/arr-right-hover.svg")
    }

    &.arr-up {
      background-image: url("../img/arr-up-hover.svg")
    }

    &.arr-down {
      background-image: url("../img/arr-down-hover.svg")
    }

    &.zoom {
      background-image: url("../img/zoom-hover.svg")
    }
  }

  &:after {
    display: none;
  }
}

.pagination {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  color: $dark;
  font-family: $font1;
  font-size: 16px;
  font-weight: 700;
  line-height: 1;

  .page-link {
    color: $dark;
    font-family: $font1;
    font-size: 16px;
    font-weight: 700;
    line-height: 1;

    &.current {
      color: #ffffff;
      background-color: $orange;
      border-radius: 100%;
      width: 50px;
      height: 50px;
      display: flex;
      align-items: center;
      justify-content: center;

      &:not(:first-child) {
        margin-left: 20px;
      }

      +* {
        margin-left: 20px;
      }
    }

    &:not(.current):hover {
      color: $orange;
    }
  }

  * {
    +* {
      margin-left: 35px;
    }
  }
}


.share-block {
  display: flex;
  flex-direction: column;
  width: 50px;
  box-shadow: 10px 17px 50px rgba(9, 32, 61, 0.2);
  border-radius: 15px;
  background-color: #ffffff;
  overflow: hidden;

  a {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 50px;
    cursor: pointer;
    position: relative;

    +a {
      &::before {
        content: "";
        display: block;
        width: 50px;
        height: 1px;
        background-color: #cccccc;
        opacity: 0.4;
        position: absolute;
        left: 0;
        top: 0;
      }
    }

    img {
      display: block;
      width: 16px;
      height: 16px;
      object-fit: contain;
    }

    &:hover {
      &::before {
        display: none;
      }

      img {
        filter: brightness(100);
      }

      &.vk {
        background-color: #6695bd;
      }

      &.fb {
        background-color: #3b5998;
      }

      &.tw {
        background-color: #00a1f4;
      }

      &.ok {
        background-color: #ff9205;
      }

      &.vb {
        background-color: #7b519d;
      }

      &.wa {
        background-color: #65bc54;
      }
    }
  }
}


.slider-pagination {
  .swiper-pagination-bullet {
    width: 14px;
    height: 14px;
    background-color: $dark;
    opacity: 0.15;
    margin: 0 8px;
  }

  .swiper-pagination-bullet-active {
    background-color: $orange;
    opacity: 1;
  }

  .swiper-pagination-bullet:not(.swiper-pagination-bullet-active):hover {
    background-color: $orange;
    opacity: 0.2;
  }
}

.pagi {
  margin: 23px -2.5px 0;
  display: flex;

  >a {
    display: block;
    width: 46px;
    height: 46px;
    border-radius: 50%;
    margin: 0 2.5px;
    text-align: center;
    color: #333333;
    font-size: 16px;
    font-weight: 700;
    line-height: 46px;
    padding: 2px;

  }

  &__dots {
    pointer-events: none;
  }

  &__page {
    &--active {
      pointer-events: none;
      background-color: #ea961c;
      color: #ffffff !important;
    }

    &:hover {
      padding: 0;
      border: 2px solid #ea961c;
    }
  }

  &__next {
    margin-left: 20px;
    width: 50px;
    height: 50px;
    box-sizing: border-box;
    padding: 0;
    background: url(../img/orange-arrow-right.png) no-repeat center center;

    &:hover {
      border: 2px solid #ea961c;
    }
  }
}

@media (max-width:450px) {
  .pagi {
    justify-content: center;
    margin: 0;

    >a {
      width: 26px;
      height: 26px;
      line-height: 26px;

    }





    &__next {
      margin-left: 10px;
      width: 30px;
      height: 30px;
    }
  }
}
